import React, { useState, useEffect } from 'react'
import CountUp from 'react-countup'
import styled, { keyframes } from "styled-components";
import './covid.css'
import covidHP from "../imgs/covidHP.svg"
import { GiDeathSkull } from "react-icons/gi";
import coronaIcon from "../imgs/coronaIcon.svg"

function CovidSK() {
    const [dataSvk, setDataSvk] = useState()

    const svk = 'https://mapa.covid.chat/map_data'

    useEffect(() => {
        LoadData()
    }, [])

    const negatTestyMoje = [83, 102, 50, 42, 120, 129, 118, 197, 228]
    const LoadData = async () => {
        const response = await fetch(svk)
        const data = await response.json()
        setDataSvk([data])
    }

    function handleData() {
        return (
            <div>
                {/* Počet testovaných: {dataSvk[0].tested} <br />
                Počet nakazených: {dataSvk[0].infected}<br />
                Počet mŕtvych: {dataSvk[0].deaths}<br />
                Počet vyliečených: {dataSvk[0].recovered}<br />
 */}

                <div className="cardsDiv">
                    <div className="cards">
                        <div className="topCard">
                            <div className="obrDiv">
                                <div className="circle">
                                    <img alt="" src={coronaIcon} />
                                </div>
                            </div>
                            <div className="textCard">Slovenské údaje</div>
                        </div>
                        <div className="mainTextSK">
                            Počet testovaných: {dataSvk[0].tested}<br />
                Počet negatívnych testov: {dataSvk[0].tested - dataSvk[0].infected}<br />
                Počet pozitívnych testov: {dataSvk[0].infected}<br />
                Počet vyliečených: {dataSvk[0].recovered}<br />
                            {/* Počet mŕtvych: {dataSvk[0].deaths}<br /> */}
                        </div>
                    </div>

                    {/* 
                    <div className="cards">
                        <div className="topCard">
                            <div className="obrDiv">
                                <div className="circle HP">
                                    <img alt="" src={covidHP} />
                                </div>
                            </div>
                            <div className="textCard"> Počet vyliečených</div>
                        </div>
                        <div className="mainText">
                        </div>
                    </div>


                    <div className="cards">
                        <div className="topCard">
                            <div className="obrDiv">
                                <div className="circle skull">
                                    <GiDeathSkull color={"white"} />
                                </div>
                            </div>
                            <div className="textCard"> Počet úmrtí</div>
                        </div>
                        <div className="mainText">
                        </div>
                    </div> */}
                </div>
            </div>
        )
    }
    const Heading = keyframes`
    0%{
        height: 0%;
      }
      100%{
        height: 100%;
      }
    `;

    const HomeHeader = styled.div`
    height: 100%;
    animation: ${Heading} 2s;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    `;

    function handleGraph1() {
        const MaxInfected = 100 / dataSvk[0].chart[Object.keys(dataSvk[0].chart).length - 1].infected
        return (
            <div className="outsideDivGraf">
                <div className="svkGraf">
                    {
                        dataSvk[0].chart.map((day1, index) =>
                            <HomeHeader key={index}>
                                <div className="stlpcePrvehoGrafu" style={{ height: `calc(${MaxInfected * day1.infected}% )` }} >
                                    <CountUp className="CountPrvehoGrafu" end={parseInt(day1.infected)} />
                                    {dataSvk[0].tested_chart[index - 1]
                                        ? <CountUp className="countupFirstRed" prefix="+" end={parseInt(dataSvk[0].tested_chart[index - 1].infected)} />
                                        : <CountUp className="countupFirstRed" prefix="+" end={1} />}
                                    <div className="spanDate">{day1.date}</div>
                                </div>
                            </HomeHeader>
                        )
                    }
                    {/* <div className="testDElete" style={{ bottom: `calc(${MaxInfected * 150}% )` }} ></div>
                    <div className="borderTextNaGrafe" style={{ bottom: `calc(${MaxInfected * 150}% )` }} >150</div> */}
                </div>
                <div className="medzeraPodDatumomGraf1"></div>
            </div>
        )
    }

    function handleGraph2() {
        let MaxDaily = 0
        if (dataSvk) {
            dataSvk[0].tested_chart.map(pom1 => {
                if (pom1.tested > MaxDaily) {
                    MaxDaily = pom1.tested - pom1.infected
                }
            })
            MaxDaily = MaxDaily / 100
        }
        return (
            <div className="outsideDivGraf">
                <div className="svkGraf">
                    <div className="testDElete"  ></div>

                    {<HomeHeader >  {/* druhý stlpec pre druhy graf */}
                        <div className="PozitivneTestyDruhyGraf" style={{ height: ` 3px` }} >
                            <CountUp className="countup CountDruhehoGrafu graf2" end={1} />
                            <div className="spanDate">{"06-03-2020"}</div>
                        </div>
                        <div className="dayX2 dayX2pom" style={{ height: `calc(${306 / MaxDaily}% + 3px)` }} >
                            <CountUp className="countupX2 CountDruhehoGrafu" end={306} />
                        </div>
                    </HomeHeader>}

                    {dataSvk[0].tested_chart.map((day1, index) =>
                        <HomeHeader key={index}>
                            {
                                <>
                                    {day1.infected === 0
                                        ?
                                        <div className="PozitivneTestyDruhyGraf"  >
                                            <CountUp className="countup2 CountDruhehoGrafu" end={day1.infected} />
                                            <div className="spanDate">{day1.date}</div>
                                        </div>
                                        :
                                        <div className="PozitivneTestyDruhyGraf" style={{ height: `calc(${(day1.infected) / MaxDaily}% + 3px)` }} >
                                            <CountUp className="countup2 CountDruhehoGrafu" end={day1.infected} />
                                            <div className="spanDate">{day1.date}</div>
                                        </div>
                                    }
                                    {
                                        negatTestyMoje[index]
                                            ?
                                            <div className="dayX2" style={{ height: `calc(${negatTestyMoje[index] / MaxDaily}% + 3px)` }} >
                                                {(negatTestyMoje[index] > 99) ? <>
                                                    <CountUp className="countupX2 CountDruhehoGrafu" end={negatTestyMoje[index]} style={{ marginLeft: '-2px' }} />
                                                </>
                                                    : <CountUp className="countupX2 CountDruhehoGrafu" end={negatTestyMoje[index]} />}
                                            </div>
                                            : <div className="dayX2" style={{ height: `calc(${day1.tested / MaxDaily}% + 3px)` }} >
                                                <CountUp className="countupX2 CountDruhehoGrafu" end={day1.tested - day1.infected} />
                                            </div>
                                    }
                                </>
                            }
                        </HomeHeader>
                    )}
                </div>
                <div className="vysvetlivkyPodGrafom">
                    <div><div className="blue"></div>Počet negatívnych testov</div>
                    <div><div className="red"></div>Počet pozitívnych testov</div>
                </div>
            </div>
        )
    }

    return (
        <div className="covidApp">
            {
                dataSvk
                    ? <>
                        {handleData()}
                        {handleGraph1()}
                        {handleGraph2()}
                    </>
                    : ''


            }
        </div >
    )
}

export default CovidSK
