import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { FaSortAmountDown } from 'react-icons/fa';

function CovidTable(props) {
    const [ikonka, setIkonka] = useState('cases')
    let tableStyleWorld = {
        background: 'grey'
    }
    function zmenIkonku(key) {
        if (key === ikonka) {
            setIkonka(key + "2")
        }
        else { setIkonka(key) }
    }

    const myStyle = {

    }

    const myStyle2 = {
        transform: "rotateX(180deg)",
    }


    function myFunction() {
        var input, filter, table, tr, td, i, txtValue;
        input = document.getElementById("myInput");
        filter = input.value.toUpperCase();
        table = document.getElementById("myTable");
        tr = table.getElementsByTagName("tr");
        for (i = 0; i < tr.length; i++) {
            td = tr[i].getElementsByTagName("td")[0];
            if (td) {
                txtValue = td.textContent || td.innerText;
                if (txtValue.toUpperCase().indexOf(filter) > -1) {
                    tr[i].style.display = "";
                } else {
                    tr[i].style.display = "none";
                }
            }
        }
    }

    function FormatNum(num) {
        if (num) {
            return (
                num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
            )
        }
    }

    function functionForWorld(item, id) {
        return (
            <tr key={id} style={{ background: 'rgb(214, 214, 214)' }} >
                <td style={{ width: '100px' }}>{item.country}</td>
                <td>{FormatNum(item.cases)}</td>
                <td>{FormatNum(item.active)}</td>
                <td style={{ background: ' rgb(254, 255, 194, .4)' }}>+{FormatNum(item.todayCases)} </td>
                <td>{FormatNum(item.deaths)}</td>
                <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+{FormatNum(item.todayDeaths)} </td>
                <td style={{ fontWeight: 'bold', color: 'green' }}>{FormatNum(item.recovered)}</td>
            </tr>
        )
    }
    return (
        // <div className="divTable" style={{ overflowX: 'visible' }}>
        <>

            <input type="text" id="myInput" onKeyUp={myFunction} placeholder="Hľadať.." className="inputSearch" />
            <div className="divTable" >
                <table id="myTable">
                    <thead>
                        <tr>
                            <th onClick={() => (props.sortBy('totalTests'), zmenIkonku('totalTests'))} >
                                Krajina
                            {ikonka === 'totalTests'
                                    ? <FaSortAmountDown style={myStyle} className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'totalTests2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('cases'), zmenIkonku('cases'))} >
                                Počet nakazených
                            {ikonka === 'cases'
                                    ? <FaSortAmountDown style={myStyle} className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'cases2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('active'), zmenIkonku('active'))} >
                                Aktívne prípady
                            {ikonka === 'active'
                                    ? <FaSortAmountDown style={myStyle} className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'active2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('todayCases'), zmenIkonku('todayCases'))} >
                                Nové prípady
                            {ikonka === 'todayCases'
                                    ? <FaSortAmountDown style={myStyle} className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'todayCases2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('deaths'), zmenIkonku('deaths'))} >
                                Počet úmrtí
                            {ikonka === 'deaths'
                                    ? <FaSortAmountDown style={myStyle} className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'deaths2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('todayDeaths'), zmenIkonku('todayDeaths'))} >
                                Nové úmrtia
                            {ikonka === 'todayDeaths'
                                    ? <FaSortAmountDown style={myStyle} className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'todayDeaths2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('recovered'), zmenIkonku('recovered'))} >
                                Počet vyliečených
                            {ikonka === 'recovered'
                                    ? <FaSortAmountDown style={myStyle} className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'recovered2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.countriesData.map((item, id) => (
                            item.country === 'World' ? functionForWorld(item, id) :
                                <tr key={id} >
                                    <td style={{ width: '100px' }}>
                                        {item.country === 'Slovakia' ?
                                            <div style={{ fontWeight: 'bold', textDecoration: 'underline' }}>
                                                <Link to='/Covid/CovidSK'>{item.country}</Link>
                                            </div>
                                            : item.country
                                        }
                                        {item.totalTests > 0 ?
                                            <div className="firstCase">Počet testov: {FormatNum(item.totalTests)}</div>
                                            : null}
                                    </td>
                                    <td>{FormatNum(item.cases)}</td>
                                    <td>{FormatNum(item.active)}</td>
                                    {item.todayCases > 0 ?
                                        <td style={{ background: ' rgb(254, 255, 194)' }}>+{FormatNum(item.todayCases)} </td>
                                        : <td></td>
                                    }
                                    <td>{FormatNum(item.deaths)}</td>
                                    {item.todayDeaths > 0 ?
                                        <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+{FormatNum(item.todayDeaths)} </td>
                                        : <td></td>
                                    }
                                    <td style={{ fontWeight: 'bold', color: 'green' }}>{FormatNum(item.recovered)}</td>
                                </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </>
    )
}

export default CovidTable