import React, { useState, useEffect } from 'react'
import './covid.css'
import './covidDesign.css'
import { Link } from 'react-router-dom'
import coronaIcon from "../imgs/coronaIcon.svg"
import CovidTable from "./CovidTable"
import covidHP from "../imgs/covidHP.svg"
import { GiDeathSkull } from "react-icons/gi";

function CovidApp() {
    // const url = 'https://covid19.mathdro.id/api/'
    const url = 'https://coronavirus-19-api.herokuapp.com/all'
    // const svk = 'https://covid19.mathdro.id/api/countries/slovakia'
    const [mainData, setMainData] = useState([])


    const urlCountries = 'https://coronavirus-19-api.herokuapp.com/countries'
    const [countriesData, setCountriesData] = useState([])
    const [state, setState] = useState({
        countriesData: countriesData,
        direction: {
            price_usd: 'asc',
        }
    })


    useEffect(() => {
        HandleLoadData()
        HandleLoadData2()
    }, [])

    const HandleLoadData = async () => {
        const response = await fetch(url)
        const data = await response.json()
        // setMainData([data.confirmed.value, data.recovered.value, data.deaths.value])
        setMainData([data.cases, data.recovered, data.deaths])
    }

    const HandleLoadData2 = async () => {
        const response = await fetch(urlCountries)
        const data = await response.json()
        setCountriesData(data)

        setState({
            data: data.sort((a, b) => (
                state.direction['cases'] === 'asc'
                    ? parseFloat(a['cases']) - parseFloat(b['cases'])
                    : parseFloat(b['cases']) - parseFloat(a['cases'])
            )),
            direction: {
                ['cases']: state.direction['cases'] === 'asc'
                    ? 'desc'
                    : 'asc'
            }
        })
    }

    function FormatNum(num) {
        if (num) {
            return (
                num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
            )
        }
    }

    function numberTrans3(a, b) {
        if (a) {
            let output = b / (a / 100)
            output = b / (a / 100)
            output = output.toFixed(3)
            output = output.toString()
            output = output.replace(".", ",")
            return (
                <div className="aktiveNakazenych">
                    <span>V percentách:</span> {output}%
                </div>
            )
        }
    }

    function sortBy(key) {
        setState({
            countriesData: countriesData.sort((a, b) => (
                state.direction[key] === 'asc'
                    ? parseFloat(a[key]) - parseFloat(b[key])
                    : parseFloat(b[key]) - parseFloat(a[key])
            )),
            direction: {
                [key]: state.direction[key] === 'asc'
                    ? 'desc'
                    : 'asc'
            }
        })
    }


    return (
        <div className="covidApp">
            <div className="cardsDiv">
                <div className="cards">
                    <div className="topCard">
                        <div className="obrDiv">
                            <div className="circle">
                                <img alt="" src={coronaIcon} />
                            </div>
                        </div>
                        <div className="textCard"> Počet nakazených</div>
                    </div>
                    <div className="mainText">
                        {FormatNum(mainData[0])}
                        {/* {numberTrans2(mainData[0], mainData[1], mainData[2])} */}
                        <div className="aktiveNakazenych">
                            <span>Počet aktívnych prípadov:</span> {FormatNum(mainData[0] - mainData[1] - mainData[2])}
                        </div>
                    </div>
                </div>


                <div className="cards">
                    <div className="topCard">
                        <div className="obrDiv">
                            <div className="circle HP">
                                <img alt="" src={covidHP} />
                            </div>
                        </div>
                        <div className="textCard"> Počet vyliečených</div>
                    </div>
                    <div className="mainText">
                        {FormatNum(mainData[1])}
                        {numberTrans3(mainData[0], mainData[1])}
                    </div>
                </div>

                <div className="cards">
                    <div className="topCard">
                        <div className="obrDiv">
                            <div className="circle skull">
                                <GiDeathSkull color={"white"} />
                            </div>
                        </div>
                        <div className="textCard"> Počet úmrtí</div>
                    </div>
                    <div className="mainText">
                        {FormatNum(mainData[2])}
                        {numberTrans3(mainData[0], mainData[2])}
                    </div>
                </div>
            </div>

            <br></br>
            <br></br>

            <div className="slovenskeUdaje"><Link to='/Covid/CovidSK'>Slovenské údaje</Link></div>
            <br></br><br></br>

            <CovidTable countriesData={countriesData} sortBy={sortBy} />

        </div >
    )
}

export default CovidApp
